
import { defineComponent, reactive, toRefs } from 'vue';

interface State {
  amount: string;
}

export default defineComponent({
  name: 'AmountInput',

  setup() {
    const state = reactive<State>({
      amount: ''
    });

    const getAmount = () => state.amount;

    const setAmount = (amount: string) => {
      state.amount = amount;
    };

    return {
      ...toRefs(state),
      getAmount,
      setAmount
    }
  }
})
